//
// Luxe Theme Mode
//

$theme-luxe-colors: (
    'primary': #e9635a,
    'secondary': #9b1f8c,
    'success': #39c37d,
    'info': #2dd4bf,
    'warning': #fea142,
    'danger': #da4545,
    'purple': #6070cc,
);

@if $theme-luxe ==true {

    @import url('https://fonts.googleapis.com/css2?family=TikTok+Sans:opsz,wght@12..36,300..900&display=swap');

    html[data-skin='luxe'] {
        --#{$prefix}font-sans-serif: "TikTok Sans", sans-serif;
        --#{$prefix}font-family-secondary: "TikTok Sans", sans-serif;

        --#{$prefix}body-font-size: 13px;
        --#{$prefix}body-bg: #f9f5f5;
        --#{$prefix}secondary-bg: #ffffff;

        --#{$prefix}theme-card-border-width: 0;
        --#{$prefix}theme-card-border-color: #edf2f9;
        --#{$prefix}theme-card-box-shadow: 0 4px 9px rgba(34, 7, 31, 0.05);
        --#{$prefix}box-shadow: 0 4px 9px rgba(34, 7, 31, 0.05);
        --#{$prefix}border-radius: 6px;

        --#{$prefix}font-weight-medium: 500;
        --#{$prefix}font-weight-semibold: 500;
        --#{$prefix}font-weight-bold: 600;

        --#{$prefix}font-size-xxs: 11px;
        --#{$prefix}font-size-xs: 12px;
        --#{$prefix}font-size-base: 13px;
        --#{$prefix}font-size-md: 14px;
        --#{$prefix}font-size-lg: 15px;
        --#{$prefix}font-size-xl: 18px;
        --#{$prefix}font-size-xxl: 20px;

        // Menu items
        --#{$prefix}sidenav-item-icon-size: 1rem;
        --#{$prefix}sidenav-item-gap: 10px;
        --#{$prefix}sidenav-item-padding-x: 12px;
        --#{$prefix}sidenav-item-padding-y: 8px;
        --#{$prefix}sidenav-item-font-size: 0.875rem;
        --#{$prefix}sidenav-item-font-weight: 400;

        // Sub Menu Items
        --#{$prefix}sidenav-sub-item-font-size: 0.875rem;
        --#{$prefix}sidenav-sub-item-font-weight: 400;
        --#{$prefix}sidenav-sub-item-gap: 4px;
        --#{$prefix}sidenav-sub-item-padding-x: 10px;
        --#{$prefix}sidenav-sub-item-padding-y: 7px;

        // Light left-sidebar
        &[data-menu-color='light'] {
            --#{$prefix}sidenav-bg: #{$white};
            --#{$prefix}sidenav-border-color: #edf2f9;
            --#{$prefix}sidenav-item-color: #{$gray-700};
            --#{$prefix}sidenav-item-hover-color: #e9635a;
            --#{$prefix}sidenav-item-hover-bg: #f1f1f1;
            --#{$prefix}sidenav-item-active-color: #e9635a;
            --#{$prefix}sidenav-item-active-bg: #fdf1f0;
        }

        // Gray left-sidebar
        &[data-menu-color='gray'] {
            --#{$prefix}sidenav-bg: #f1eded;
            --#{$prefix}sidenav-border-color: #e7e9eb;
            --#{$prefix}sidenav-item-color: #{$gray-700};
            --#{$prefix}sidenav-item-hover-color: #e9635a;
            --#{$prefix}sidenav-item-hover-bg: #e9e2e2;
            --#{$prefix}sidenav-item-active-color: #212b29;
            --#{$prefix}sidenav-item-active-bg: #e9e2e2;
        }

        // Dark Left Sidebar
        &[data-menu-color='dark'] {
            --#{$prefix}sidenav-bg: #22071f;
            --#{$prefix}sidenav-border-color: #22071f;
            --#{$prefix}sidenav-item-color: #938691;
            --#{$prefix}sidenav-item-hover-color: #d5c9d4;
            --#{$prefix}sidenav-item-hover-bg: #382035;
            --#{$prefix}sidenav-item-active-color: #d5c9d4;
            --#{$prefix}sidenav-item-active-bg: #311c2e;
        }

        // Gradient Menu
        &[data-menu-color='gradient'] {
            --#{$prefix}sidenav-bg: #{linear-gradient(to right, #393a66, #361732)};
            --#{$prefix}sidenav-border-color: null;
            --#{$prefix}sidenav-item-color: #8782a9;
            --#{$prefix}sidenav-item-hover-color: #bccee4;
            --#{$prefix}sidenav-item-hover-bg: rgba(219, 236, 255, 0.05);
            --#{$prefix}sidenav-item-active-color: #cfdff1;
            --#{$prefix}sidenav-item-active-bg: rgba(219, 236, 255, 0.05);
        }

        // Image Menu
        &[data-menu-color='image'] {
            --#{$prefix}sidenav-bg: #{linear-gradient(to bottom right, rgba(16, 16, 30, 0.85), #22071f), url('../images/sidenav-bg.jpg') center/cover no-repeat};
            --#{$prefix}sidenav-border-color: null;
            --#{$prefix}sidenav-item-color: #799cb7;
            --#{$prefix}sidenav-item-hover-color: #bccee4;
            --#{$prefix}sidenav-item-hover-bg: #2f3742;
            --#{$prefix}sidenav-item-active-color: #cfdff1;
            --#{$prefix}sidenav-item-active-bg: rgba(219, 236, 255, 0.05);
        }

        // Dark Mode
        &[data-bs-theme='dark'][data-menu-color='dark'],
        &[data-bs-theme='dark'][data-menu-color='gray'],
        &[data-bs-theme='dark'][data-menu-color='light'] {
            --#{$prefix}sidenav-bg: #1e1f27;
            --#{$prefix}sidenav-border-color: #2c2d38;
            --#{$prefix}sidenav-item-color: #8495ab;
            --#{$prefix}sidenav-item-hover-color: #bccee4;
            --#{$prefix}sidenav-item-hover-bg: #2f3742;
            --#{$prefix}sidenav-item-active-color: #ced6df;
            --#{$prefix}sidenav-item-active-bg: #22232c;
        }

        // Light Topbar
        &[data-topbar-color='light'] {
            --#{$prefix}topbar-bg: #f7f3f3;
            --#{$prefix}topbar-item-color: #{$gray-700};
            --#{$prefix}topbar-item-hover-color: #e9635a;
            --#{$prefix}topbar-search-bg: transparent;
            --#{$prefix}topbar-search-border: #dbdfe3;
        }

        // Gray Topbar
        &[data-topbar-color='gray'] {
            --#{$prefix}topbar-bg: #e9e0e2;
            --#{$prefix}topbar-item-color: #{$gray-700};
            --#{$prefix}topbar-item-hover-color: #e9635a;
            --#{$prefix}topbar-search-bg: #e1d0d4;
            --#{$prefix}topbar-search-border: #e1d0d4;
        }

        // Dark Topbar
        &[data-topbar-color='dark'] {
            --#{$prefix}topbar-bg: #22071f;
            --#{$prefix}topbar-item-color: #bbb0b9;
            --#{$prefix}topbar-item-hover-color: #f9e5f5;
            --#{$prefix}topbar-search-bg: #392035;
            --#{$prefix}topbar-search-border: #392035;
        }

        // Gradient Topbar
        &[data-topbar-color='gradient'] {
            --#{$prefix}topbar-bg: #{linear-gradient(to right, #393a66, #6c2063, #52174b)};
            --#{$prefix}topbar-item-color: rgba(255, 255, 255, 0.7);
            --#{$prefix}topbar-item-hover-color: #ffffff;
            --#{$prefix}topbar-search-bg: rgba(255, 255, 255, 0.1);
            --#{$prefix}topbar-search-border: rgba(255, 255, 255, 0.1);
        }

        // Topbar (Dark Mode)
        &[data-bs-theme='dark'][data-topbar-color='dark'],
        &[data-bs-theme='dark'][data-topbar-color='light'],
        &[data-bs-theme='dark'][data-topbar-color='gray'] {
            --#{$prefix}topbar-bg: #252630;
            --#{$prefix}topbar-item-color: #adb5bf;
            --#{$prefix}topbar-item-hover-color: #e0eeff;
            --#{$prefix}topbar-search-bg: #2d2e3c;
            --#{$prefix}topbar-search-border: #2d2e3c;
        }

        @each $name, $value in $theme-luxe-colors {
            --#{$prefix}#{$name}: #{$value};
        }

        @each $name, $value in $theme-luxe-colors {
            --#{$prefix}#{$name}-rgb: #{to-rgb($value)};
        }

        @each $name, $value in $theme-luxe-colors {
            --#{$prefix}#{$name}-bg-subtle: #{rgba($value, 0.15)};
        }

        @each $name, $value in $theme-luxe-colors {
            --#{$prefix}#{$name}-border-subtle: #{rgba($value, 0.3)};
        }

        @each $name, $value in $theme-luxe-colors {
            --#{$prefix}#{$name}-text-emphasis: #{shade-color($value, 25%)};
        }

        // Chart Colors
        --#{$prefix}chart-primary: #e9635a; // Main Color
        --#{$prefix}chart-primary-rgb: #{to-rgb(#e9635a)};
        --#{$prefix}chart-secondary: #9b1f8c; // Secondary Color
        --#{$prefix}chart-secondary-rgb: #{to-rgb(#9b1f8c)};
        --#{$prefix}chart-alpha: #39c37d; // Success Color
        --#{$prefix}chart-alpha-rgb: #{to-rgb(#39c37d)};
        --#{$prefix}chart-beta: #da4545; // Danger Color
        --#{$prefix}chart-beta-rgb: #{to-rgb(#da4545)};
        --#{$prefix}chart-gamma: #fea142; // Warning Color
        --#{$prefix}chart-gamma-rgb: #{to-rgb(#fea142)};
        --#{$prefix}chart-delta: #2dd4bf; // Info Color
        --#{$prefix}chart-delta-rgb: #{to-rgb(#2dd4bf)};
        --#{$prefix}chart-zeta: #6070cc; // Purple or custom Color
        --#{$prefix}chart-zeta-rgb: #{to-rgb(#6070cc)};
        --#{$prefix}chart-gray: #9b1f8c; // Gray Color
        --#{$prefix}chart-gray-rgb: #{to-rgb(#9b1f8c)};
        --#{$prefix}chart-dark: #396699; // Primary Dark Color
        --#{$prefix}chart-dark-rgb: #{to-rgb(#396699)};
        --#{$prefix}chart-border-color: #ecf4fc; // Border Color
        --#{$prefix}chart-title-color: #bbcae1; // Title Color
    }

    @if $enable-dark-mode {
        @include color-mode(dark, true) {
            &[data-skin='luxe'] {
                --#{$prefix}body-bg: #{#17181e};
                --#{$prefix}body-bg-rgb: #{to-rgb(#17181e)};

                --#{$prefix}secondary-bg: #{#1b1c22};
                --#{$prefix}secondary-bg-rgb: #{to-rgb(#1b1c22)};

                --#{$prefix}box-shadow: #{0px 0px 30px rgba(0, 0, 0, 0.3)};

                --#{$prefix}theme-card-border-color: #293036;

                --#{$prefix}chart-gray: #40424f;
                --#{$prefix}chart-gray-rgb: #{to-rgb(#40424f)};

                // Extra CSS
                .card,
                .dropdown-menu {
                    border-width: 1px !important;
                }
            }
        }
    }
}

//
//  Luxe Theme
//
html[data-skin='luxe'] {

    .app-topbar {
        box-shadow: none;
    }

    .page-title-head {
        position: relative;

        &::before {
            content: "";
            height: 150px;
            width: 100%;
            top: 0;
            position: absolute;
            background: var(--#{$prefix}topbar-bg);
            inset-inline-start: 0;
            z-index: -1;
        }
    }

    .pro-username {
        font-size: 13px;
    }
}

html[data-skin="luxe"]:not([data-sidenav-size="condensed"]) {
    .side-nav {

        .side-nav-item {
            .side-nav-link {

                .menu-icon {
                    line-height: 26px;
                    height: 24px;
                    width: 24px;
                    border-radius: 50%;
                    background-color: $sidenav-item-hover-bg;
                    text-align: center;
                }
            }
        }

        // Multi Level Menu
        .sub-menu {
            padding-inline-start: 8px;

            .sub-menu {
                padding-inline-start: 22px;
            }
        }
    }
}

// Manage for light & dark logo
html[data-skin="luxe"][data-menu-color='light'][data-topbar-color='dark'],
html[data-skin="luxe"][data-menu-color='light'][data-topbar-color='gradient'] {
    .sidenav-menu {
        .logo {
            background: $sidenav-bg;
            width: 100%;
        }

        .sidenav-user {
            margin-top: 0;
        }

        .button-on-hover {
            color: $sidenav-item-color !important;

            &:hover {
                color: $sidenav-item-hover-color !important;
            }
        }
    }

    &:not([data-bs-theme='dark']) {
        .logo {
            &.logo-light {
                display: none;
            }

            &.logo-dark {
                display: block;
            }
        }
    }
}


html[data-skin="luxe"][data-topbar-color='dark'],
html[data-skin="luxe"][data-topbar-color='gradient'] {
    .page-title-head {
        color: $white;

        .breadcrumb-item a {
            color: rgba(255, 255, 255, 0.5);
        }
    }
}