//
// Xenon Theme Mode
//

$theme-xenon-colors: (
    'primary': #2D3C59,
    'secondary': #1D546D,
    'success': #5F9598,
    'info': #3F9AAE,
    'warning': #D97D55,
    'danger': #D34E4E
);

@if $theme-xenon ==true {

    @import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600;1,700&display=swap');

    html[data-skin='xenon'] {
        --#{$prefix}font-sans-serif: "IBM Plex Mono", monospace;
        --#{$prefix}font-family-secondary: "IBM Plex Mono", monospace;

        --#{$prefix}body-font-size: 13px;
        --#{$prefix}body-bg: #f4f6f9;
        --#{$prefix}secondary-bg: #ffffff;

        --#{$prefix}theme-card-border-width: 0;
        --#{$prefix}theme-card-border-color: #edf2f9;
        --#{$prefix}theme-card-box-shadow: 0 1px 2px rgba(57, 62, 80, 0.15);
        --#{$prefix}box-shadow: 0 1px 3px rgba(57, 62, 80, 0.2);
        --#{$prefix}border-radius: 6px;

        --#{$prefix}font-weight-medium: 600;
        --#{$prefix}font-weight-semibold: 600;
        --#{$prefix}font-weight-bold: 700;

        --#{$prefix}font-size-xxs: 11px;
        --#{$prefix}font-size-xs: 12px;
        --#{$prefix}font-size-base: 13px;
        --#{$prefix}font-size-md: 14px;
        --#{$prefix}font-size-lg: 16px;
        --#{$prefix}font-size-xl: 18px;
        --#{$prefix}font-size-xxl: 20px;

        // Menu items
        --#{$prefix}sidenav-item-icon-size: 1rem;
        --#{$prefix}sidenav-item-gap: 10px;
        --#{$prefix}sidenav-item-padding-x: 12px;
        --#{$prefix}sidenav-item-padding-y: 10px;
        --#{$prefix}sidenav-item-font-size: 13px;
        --#{$prefix}sidenav-item-font-weight: 500;

        // Sub Menu Items
        --#{$prefix}sidenav-sub-item-font-size: 13px;
        --#{$prefix}sidenav-sub-item-font-weight: 400;
        --#{$prefix}sidenav-sub-item-gap: 4px;
        --#{$prefix}sidenav-sub-item-padding-x: 10px;
        --#{$prefix}sidenav-sub-item-padding-y: 7px;

        // Light left-sidebar
        &[data-menu-color='light'] {
            --#{$prefix}sidenav-bg: #{$white};
            --#{$prefix}sidenav-border-color: #edf2f9;
            --#{$prefix}sidenav-item-color: #{$gray-700};
            --#{$prefix}sidenav-item-hover-color: #2D3C59;
            --#{$prefix}sidenav-item-hover-bg: #f4f6f9;
            --#{$prefix}sidenav-item-active-color: #2D3C59;
            --#{$prefix}sidenav-item-active-bg: #f4f6f9;
        }

        // Gray left-sidebar
        &[data-menu-color='gray'] {
            --#{$prefix}sidenav-bg: #e4e9f1;
            --#{$prefix}sidenav-border-color: #e4e9f1;
            --#{$prefix}sidenav-item-color: #{$gray-700};
            --#{$prefix}sidenav-item-hover-color: #212b29;
            --#{$prefix}sidenav-item-hover-bg: #dadfe5;
            --#{$prefix}sidenav-item-active-color: #212b29;
            --#{$prefix}sidenav-item-active-bg: #dadfe5;
        }

        // Dark Left Sidebar
        &[data-menu-color='dark'] {
            --#{$prefix}sidenav-bg: #061E29;
            --#{$prefix}sidenav-border-color: #061E29;
            --#{$prefix}sidenav-item-color: #8495ab;
            --#{$prefix}sidenav-item-hover-color: #bccee4;
            --#{$prefix}sidenav-item-hover-bg: rgba(255, 255, 255, .05);
            --#{$prefix}sidenav-item-active-color: #ced6df;
            --#{$prefix}sidenav-item-active-bg: rgba(255, 255, 255, .05);
        }

        // Gradient Menu
        &[data-menu-color='gradient'] {
            --#{$prefix}sidenav-bg: #{linear-gradient(to right, #2d3c59, #1d546d)};
            --#{$prefix}sidenav-border-color: null;
            --#{$prefix}sidenav-item-color: #7386a3;
            --#{$prefix}sidenav-item-hover-color: #b3c9eb;
            --#{$prefix}sidenav-item-hover-bg: rgba(219, 236, 255, 0.05);
            --#{$prefix}sidenav-item-active-color: #cfdff1;
            --#{$prefix}sidenav-item-active-bg: rgba(219, 236, 255, 0.05);
        }

        // Image Menu
        &[data-menu-color='image'] {
            --#{$prefix}sidenav-bg: #{linear-gradient(to bottom right, rgba(16, 16, 30, 0.85), #183c52), url('../images/sidenav-bg.jpg') center/cover no-repeat};
            --#{$prefix}sidenav-border-color: null;
            --#{$prefix}sidenav-item-color: #799cb7;
            --#{$prefix}sidenav-item-hover-color: #bccee4;
            --#{$prefix}sidenav-item-hover-bg: #2f3742;
            --#{$prefix}sidenav-item-active-color: #cfdff1;
            --#{$prefix}sidenav-item-active-bg: rgba(219, 236, 255, 0.05);
        }

        // Dark Mode
        &[data-bs-theme='dark'][data-menu-color='dark'],
        &[data-bs-theme='dark'][data-menu-color='gray'],
        &[data-bs-theme='dark'][data-menu-color='light'] {
            --#{$prefix}sidenav-bg: #1e1f27;
            --#{$prefix}sidenav-border-color: #2c2d38;
            --#{$prefix}sidenav-item-color: #8495ab;
            --#{$prefix}sidenav-item-hover-color: #bccee4;
            --#{$prefix}sidenav-item-hover-bg: #2f3742;
            --#{$prefix}sidenav-item-active-color: #ced6df;
            --#{$prefix}sidenav-item-active-bg: #22232c;
        }

        // Light Topbar
        &[data-topbar-color='light'] {
            --#{$prefix}topbar-bg: #ffffff;
            --#{$prefix}topbar-item-color: #{$gray-700};
            --#{$prefix}topbar-item-hover-color: #2D3C59;
            --#{$prefix}topbar-search-bg: transparent;
            --#{$prefix}topbar-search-border: #e7e9eb;
        }

        // Gray Topbar
        &[data-topbar-color='gray'] {
            --#{$prefix}topbar-bg: #f2f2f2;
            --#{$prefix}topbar-item-color: #{$gray-700};
            --#{$prefix}topbar-item-hover-color: #2D3C59;
            --#{$prefix}topbar-search-bg: #e4e6ed;
            --#{$prefix}topbar-search-border: #e4e6ed;
        }

        // Dark Topbar
        &[data-topbar-color='dark'] {
            --#{$prefix}topbar-bg: #354152;
            --#{$prefix}topbar-item-color: #adb5bf;
            --#{$prefix}topbar-item-hover-color: #e0eeff;
            --#{$prefix}topbar-search-bg: #2c3849;
            --#{$prefix}topbar-search-border: #2c3849;
        }

        // Gradient Topbar
        &[data-topbar-color='gradient'] {
            --#{$prefix}topbar-bg: #{linear-gradient(to bottom, #2d3c59, #1d546d)};
            --#{$prefix}topbar-item-color: #a7b8d1;
            --#{$prefix}topbar-item-hover-color: #ffffff;
            --#{$prefix}topbar-search-bg: rgba(255, 255, 255, 0.1);
            --#{$prefix}topbar-search-border: rgba(255, 255, 255, 0.1);
        }

        // Topbar (Dark Mode)
        &[data-bs-theme='dark'][data-topbar-color='dark'],
        &[data-bs-theme='dark'][data-topbar-color='light'],
        &[data-bs-theme='dark'][data-topbar-color='gray'] {
            --#{$prefix}topbar-bg: #252630;
            --#{$prefix}topbar-item-color: #adb5bf;
            --#{$prefix}topbar-item-hover-color: #e0eeff;
            --#{$prefix}topbar-search-bg: #2d2e3c;
            --#{$prefix}topbar-search-border: #2d2e3c;
        }

        @each $name, $value in $theme-xenon-colors {
            --#{$prefix}#{$name}: #{$value};
        }

        @each $name, $value in $theme-xenon-colors {
            --#{$prefix}#{$name}-rgb: #{to-rgb($value)};
        }

        @each $name, $value in $theme-xenon-colors {
            --#{$prefix}#{$name}-bg-subtle: #{rgba($value, 0.15)};
        }

        @each $name, $value in $theme-xenon-colors {
            --#{$prefix}#{$name}-border-subtle: #{rgba($value, 0.3)};
        }

        @each $name, $value in $theme-xenon-colors {
            --#{$prefix}#{$name}-text-emphasis: #{shade-color($value, 25%)};
        }

        // Chart Colors
        --#{$prefix}chart-primary: #2D3C59; // Main Color
        --#{$prefix}chart-primary-rgb: #{to-rgb(#2D3C59)};
        --#{$prefix}chart-secondary: #D97D55; // Secondary Color
        --#{$prefix}chart-secondary-rgb: #{to-rgb(#D97D55)};
        --#{$prefix}chart-alpha: #5F9598; // Success Color
        --#{$prefix}chart-alpha-rgb: #{to-rgb(#5F9598)};
        --#{$prefix}chart-beta: #D34E4E; // Danger Color
        --#{$prefix}chart-beta-rgb: #{to-rgb(#D34E4E)};
        --#{$prefix}chart-gamma: #1D546D; // Warning Color
        --#{$prefix}chart-gamma-rgb: #{to-rgb(#1D546D)};
        --#{$prefix}chart-delta: #3F9AAE; // Info Color
        --#{$prefix}chart-delta-rgb: #{to-rgb(#3F9AAE)};
        --#{$prefix}chart-zeta: #6070cc; // Purple or custom Color
        --#{$prefix}chart-zeta-rgb: #{to-rgb(#6070cc)};
        --#{$prefix}chart-gray: #edeff3; // Gray Color
        --#{$prefix}chart-gray-rgb: #{to-rgb(#edeff3)};
        --#{$prefix}chart-dark: #396699; // Primary Dark Color
        --#{$prefix}chart-dark-rgb: #{to-rgb(#396699)};
        --#{$prefix}chart-border-color: #ecf4fc; // Border Color
        --#{$prefix}chart-title-color: #bbcae1; // Title Color
    }

    @if $enable-dark-mode {
        @include color-mode(dark, true) {
            &[data-skin='xenon'] {

                --#{$prefix}body-bg: #{#17181e};
                --#{$prefix}body-bg-rgb: #{to-rgb(#17181e)};

                --#{$prefix}secondary-bg: #{#1b1c22};
                --#{$prefix}secondary-bg-rgb: #{to-rgb(#1b1c22)};

                --#{$prefix}box-shadow: #{0px 0px 30px rgba(0, 0, 0, 0.3)};

                --#{$prefix}theme-card-border-color: #293036;

                --#{$prefix}chart-primary: #456882; // Main Color
                --#{$prefix}chart-primary-rgb: #{to-rgb(#456882)};
                --#{$prefix}chart-gray: #40424f;
                --#{$prefix}chart-gray-rgb: #{to-rgb(#40424f)};

                --#{$prefix}primary: #{#456882};
                --#{$prefix}primary-rgb: #{to-rgb(#456882)};
                --#{$prefix}primary-text-emphasis: #{shade-color(#456882, 25%)};
                --#{$prefix}primary-bg-subtle: #{rgba(#456882, 0.15)};

                // Extra CSS
                .card,
                .dropdown-menu {
                    border-width: 1px !important;
                }
            }
        }
    }
}

//
//  Xenon Theme
//
html[data-skin='xenon'] {

    .container,
    .container-fluid,
    .container-lg,
    .container-md,
    .container-sm,
    .container-xl,
    .container-xxl {
        --#{$prefix}gutter-x: .5rem;
    }

    .row:not([class*="g-"]) {
        --#{$prefix}gutter-x: .5rem;
    }

    .card {
        margin-bottom: .5rem;
        box-shadow: none;

        .card-title {
            font-weight: 700;
        }
    }

    .card-h-100 {
        height: calc(100% - .5rem);
    }

    .btn {
        font-weight: var(--#{$prefix}font-weight-normal);
    }

    .app-topbar {
        box-shadow: none;

        &.topbar-active {
            box-shadow: var(--#{$prefix}box-shadow-sm);
        }
    }

    .sidenav-menu {
        box-shadow: none;
        border-inline-end: 1px solid var(--#{$prefix}sidenav-border-color);
    }

    .page-title-head {
        min-height: 50px;
    }

    .footer {
        background-color: transparent;
        border: none;
        padding: 0 .5rem .5rem .5rem;
    }
}

html[data-skin='xenon'][data-menu-color='light'][data-topbar-color='dark'],
html[data-skin='xenon'][data-menu-color='light'][data-topbar-color='gradient'] {
    .app-topbar {
        border-radius: 0;
    }

    .sidenav-menu {

        &::before {
            display: none;
        }
    }
}

html[data-skin='xenon'][dir='rtl'] {
    .sidenav-menu {
        &::before {
            transform: rotate(-90deg);
        }

        &::after {
            transform: rotate(90deg);
        }
    }
}