//
// Vivid Theme Mode
//

$theme-vivid-colors: (
    'primary': #37718E,
    'secondary': #F38BA0,
    'success': #89AA97,
    'info': #5EABD6,
    'warning': #FAB57A,
    'danger': #C33C54,
    'light': #f1ebe5
);

@if $theme-vivid ==true {

    html[data-skin='vivid'] {
        --#{$prefix}font-sans-serif: "Google Sans", sans-serif;
        --#{$prefix}font-family-secondary: "Google Sans", sans-serif;

        --#{$prefix}body-font-size: 13px;
        --#{$prefix}body-bg: #fffaf4;
        --#{$prefix}secondary-bg: #fbf7f3;

        --#{$prefix}tertiary-bg: #f7f3ee;

        --#{$prefix}secondary-color: #7e7591;

        --#{$prefix}theme-card-border-width: 1px;
        --#{$prefix}theme-card-border-color: #e7e3de;
        --#{$prefix}theme-card-box-shadow: 1px 0.1rem 0.5rem 0 rgba(48, 45, 59, 0.05);
        --#{$prefix}box-shadow: 1px 0.1rem 0.5rem 0 rgba(48, 45, 59, 0.15);
        --#{$prefix}border-radius: 8px;
        --#{$prefix}border-radius-sm: 6px;

        --#{$prefix}border-color: #e7e3de;

        --#{$prefix}font-weight-medium: 500;
        --#{$prefix}font-weight-semibold: 500;
        --#{$prefix}font-weight-bold: 600;

        --#{$prefix}font-size-xxs: 11px;
        --#{$prefix}font-size-xs: 12px;
        --#{$prefix}font-size-base: 13px;
        --#{$prefix}font-size-md: 14px;
        --#{$prefix}font-size-lg: 16px;
        --#{$prefix}font-size-xl: 18px;
        --#{$prefix}font-size-xxl: 20px;

        // Menu items
        --#{$prefix}sidenav-item-icon-size: 1.125rem;
        --#{$prefix}sidenav-item-gap: 12px;
        --#{$prefix}sidenav-item-padding-x: 10px;
        --#{$prefix}sidenav-item-padding-y: 10px;
        --#{$prefix}sidenav-item-font-size: 0.84375rem;
        --#{$prefix}sidenav-item-font-weight: 500;

        // Sub Menu Items
        --#{$prefix}sidenav-sub-item-font-size: 0.84375rem;
        --#{$prefix}sidenav-sub-item-font-weight: 400;
        --#{$prefix}sidenav-sub-item-gap: 4px;
        --#{$prefix}sidenav-sub-item-padding-x: 10px;
        --#{$prefix}sidenav-sub-item-padding-y: 7px;

        // Background Light left-sidebar
        &[data-menu-color='light'] {
            --#{$prefix}sidenav-bg: #{$white};
            --#{$prefix}sidenav-border-color: #fffaf4;
            --#{$prefix}sidenav-item-color: #{$gray-700};
            --#{$prefix}sidenav-item-hover-color: #37718E;
            --#{$prefix}sidenav-item-hover-bg: #f7f7ff;
            --#{$prefix}sidenav-item-active-color: #37718E;
            --#{$prefix}sidenav-item-active-bg: #f7f7ff;
        }

        // Background Gray left-sidebar
        &[data-menu-color='gray'] {
            --#{$prefix}sidenav-bg: #f3efea;
            --#{$prefix}sidenav-border-color: #f3efea;
            --#{$prefix}sidenav-item-color: #{$gray-700};
            --#{$prefix}sidenav-item-hover-color: #212b29;
            --#{$prefix}sidenav-item-hover-bg: #ebe6e0;
            --#{$prefix}sidenav-item-active-color: #212b29;
            --#{$prefix}sidenav-item-active-bg: #ebe6e0;
        }

        // Dark Left Sidebar
        &[data-menu-color='dark'] {
            --#{$prefix}sidenav-bg: #383131;
            --#{$prefix}sidenav-border-color: #383131;
            --#{$prefix}sidenav-item-color: #9f9f9f;
            --#{$prefix}sidenav-item-hover-color: #d5d5d5;
            --#{$prefix}sidenav-item-hover-bg: #312a2a;
            --#{$prefix}sidenav-item-active-color: #d5d5d5;
            --#{$prefix}sidenav-item-active-bg: #312a2a;
        }

        // Gradient Menu
        &[data-menu-color='gradient'] {
            --#{$prefix}sidenav-bg: #{linear-gradient(135deg, #1a455f, #262549)};
            --#{$prefix}sidenav-border-color: null;
            --#{$prefix}sidenav-item-color: #799cb7;
            --#{$prefix}sidenav-item-hover-color: #bccee4;
            --#{$prefix}sidenav-item-hover-bg: #2f3742;
            --#{$prefix}sidenav-item-active-color: #cfdff1;
            --#{$prefix}sidenav-item-active-bg: #294461;
        }

        // Image Menu
        &[data-menu-color='image'] {
            --#{$prefix}sidenav-bg: #{linear-gradient(to bottom right, rgba(16, 16, 30, 0.85), #183c52), url('../images/sidenav-bg.jpg') center/cover no-repeat};
            --#{$prefix}sidenav-border-color: null;
            --#{$prefix}sidenav-item-color: #799cb7;
            --#{$prefix}sidenav-item-hover-color: #bccee4;
            --#{$prefix}sidenav-item-hover-bg: #2f3742;
            --#{$prefix}sidenav-item-active-color: #cfdff1;
            --#{$prefix}sidenav-item-active-bg: #1e3447;
        }

        // Dark Mode
        &[data-bs-theme='dark'][data-menu-color='dark'],
        &[data-bs-theme='dark'][data-menu-color='gray'],
        &[data-bs-theme='dark'][data-menu-color='light'] {
            --#{$prefix}sidenav-bg: #1e1f27;
            --#{$prefix}sidenav-border-color: #2c2d38;
            --#{$prefix}sidenav-item-color: #8495ab;
            --#{$prefix}sidenav-item-hover-color: #bccee4;
            --#{$prefix}sidenav-item-hover-bg: #2f3742;
            --#{$prefix}sidenav-item-active-color: #ced6df;
            --#{$prefix}sidenav-item-active-bg: #22232c;
        }

        // Light Topbar
        &[data-topbar-color='light'] {
            --#{$prefix}topbar-bg: #f5f1ee;
            --#{$prefix}topbar-item-color: #{$gray-700};
            --#{$prefix}topbar-item-hover-color: #37718E;
            --#{$prefix}topbar-search-bg: #edeae7;
            --#{$prefix}topbar-search-border: #edeae7;
        }

        // Gray Topbar
        &[data-topbar-color='gray'] {
            --#{$prefix}topbar-bg: #fffaf4;
            --#{$prefix}topbar-item-color: #{$gray-700};
            --#{$prefix}topbar-item-hover-color: #37718E;
            --#{$prefix}topbar-search-bg: #f3f1ed;
            --#{$prefix}topbar-search-border: #f3f1ed;
        }

        // Dark Topbar
        &[data-topbar-color='dark'] {
            --#{$prefix}topbar-bg: #383131;
            --#{$prefix}topbar-item-color: #e9ecf1;
            --#{$prefix}topbar-item-hover-color: #e0eeff;
            --#{$prefix}topbar-search-bg: #443f3f;
            --#{$prefix}topbar-search-border: #443f3f;
        }

        // Gradient Topbar
        &[data-topbar-color='gradient'] {
            --#{$prefix}topbar-bg: #{linear-gradient(to bottom, #37718E, #3a3052)};
            --#{$prefix}topbar-item-color: rgba(255, 255, 255, 0.7);
            --#{$prefix}topbar-item-hover-color: #ffffff;
            --#{$prefix}topbar-search-bg: rgba(255, 255, 255, 0.1);
            --#{$prefix}topbar-search-border: rgba(255, 255, 255, 0.1);
        }

        // Topbar (Dark Mode)
        &[data-bs-theme='dark'][data-topbar-color='dark'],
        &[data-bs-theme='dark'][data-topbar-color='light'],
        &[data-bs-theme='dark'][data-topbar-color='gray'] {
            --#{$prefix}topbar-bg: #252630;
            --#{$prefix}topbar-item-color: #adb5bf;
            --#{$prefix}topbar-item-hover-color: #e0eeff;
            --#{$prefix}topbar-search-bg: #2d2e3c;
            --#{$prefix}topbar-search-border: #2d2e3c;
        }

        @each $name, $value in $theme-vivid-colors {
            --#{$prefix}#{$name}: #{$value};
        }

        @each $name, $value in $theme-vivid-colors {
            --#{$prefix}#{$name}-rgb: #{to-rgb($value)};
        }

        @each $name, $value in $theme-vivid-colors {
            --#{$prefix}#{$name}-bg-subtle: #{rgba($value, 0.15)};
        }

        @each $name, $value in $theme-vivid-colors {
            --#{$prefix}#{$name}-border-subtle: #{rgba($value, 0.3)};
        }

        @each $name, $value in $theme-vivid-colors {
            --#{$prefix}#{$name}-text-emphasis: #{shade-color($value, 25%)};
        }

        // Chart Colors
        --#{$prefix}chart-primary: #37718E; // Main Color
        --#{$prefix}chart-primary-rgb: #{to-rgb(#37718E)};
        --#{$prefix}chart-secondary: #F38BA0; // Secondary Color
        --#{$prefix}chart-secondary-rgb: #{to-rgb(#F38BA0)};
        --#{$prefix}chart-alpha: #89AA97; // Success Color
        --#{$prefix}chart-alpha-rgb: #{to-rgb(#89AA97)};
        --#{$prefix}chart-beta: #C33C54; // Danger Color
        --#{$prefix}chart-beta-rgb: #{to-rgb(#C33C54)};
        --#{$prefix}chart-gamma: #FAB57A; // Warning Color
        --#{$prefix}chart-gamma-rgb: #{to-rgb(#FAB57A)};
        --#{$prefix}chart-delta: #5EABD6; // Info Color
        --#{$prefix}chart-delta-rgb: #{to-rgb(#5EABD6)};
        --#{$prefix}chart-zeta: #6B3F69; // Purple or custom Color
        --#{$prefix}chart-zeta-rgb: #{to-rgb(#6B3F69)};
        --#{$prefix}chart-gray: #f1eeea; // Gray Color
        --#{$prefix}chart-gray-rgb: #{to-rgb(#f1eeea)};
        --#{$prefix}chart-dark: #396699; // Primary Dark Color
        --#{$prefix}chart-dark-rgb: #{to-rgb(#396699)};
        --#{$prefix}chart-border-color: #ecf4fc; // Border Color
        --#{$prefix}chart-title-color: #bbcae1; // Title Color
    }

    @if $enable-dark-mode {
        @include color-mode(dark, true) {
            &[data-skin='vivid'] {
                --#{$prefix}body-bg: #{#17181e};
                --#{$prefix}body-bg-rgb: #{to-rgb(#17181e)};

                --#{$prefix}light: #252630;
                --#{$prefix}light-rgb: #{to-rgb(#252630)};

                --#{$prefix}secondary-bg: #{#1b1c22};
                --#{$prefix}secondary-bg-rgb: #{to-rgb(#1b1c22)};

                --#{$prefix}box-shadow: #{0px 0px 30px rgba(0, 0, 0, 0.3)};

                --#{$prefix}theme-card-border-color: #293036;

                --#{$prefix}tertiary-bg: #252630;

                --#{$prefix}secondary-color: #8391a2;

                --#{$prefix}border-color: #293036;

                --#{$prefix}chart-gray: #40424f;
                --#{$prefix}chart-gray-rgb: #{to-rgb(#40424f)};

                .dropdown-menu,
                .offcanvas {
                    background-color: #1b1c22;
                }
            }
        }
    }
}

//
//  Vivid Theme
//
html[data-skin='vivid'] {


    .card {
        border-radius: 12px;
        border-style: dashed;
    }

    .sidenav-menu,
    .app-topbar,
    .card {
        box-shadow: none;
    }

    .footer {
        background-color: transparent;
        border: none;
    }

    .dropdown-menu,
    .offcanvas {
        background-color: $white;
    }

    .btn {
        --#{$prefix}btn-padding-y: 0.4532rem;
        --#{$prefix}btn-padding-x: 1rem;
    }

    .btn-sm {
        --#{$prefix}btn-padding-y: 0.3125rem;
        --#{$prefix}btn-padding-x: 0.625rem;
    }

    .side-nav>.side-nav-item.active>a {
        border-radius: 50px;
    }

    .bg-body-secondary {
        background-color: var(--#{$prefix}secondary-bg) !important;
    }

    .pro-username {
        font-size: 13px;
        font-weight: var(--#{$prefix}font-weight-bold);
    }

    .app-topbar {

        .topbar-menu {

            .topbar-item {
                .topbar-link {
                    padding: 0;

                    .topbar-link-icon {
                        font-size: 18px;
                        height: 32px;
                        width: 32px;
                        background-color: var(--#{$prefix}topbar-search-bg);
                        border-radius: 50rem;
                        line-height: 32px;
                    }
                }
            }
        }
    }
}