//
// Neo Theme Mode
//

$theme-neo-colors: (
    'primary': #4f7fb3,
    'secondary': #97a9bd,
    'success': #6fbfa5,
    'info': #6db6e6,
    'warning': #e6c06f,
    'danger': #e38c8c
);

@if $theme-neo ==true {

    @import url('https://fonts.googleapis.com/css2?family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&display=swap');

    html[data-skin='neo'] {
        --#{$prefix}font-sans-serif: "Inter", sans-serif;
        --#{$prefix}font-family-secondary: "Inter", sans-serif;

        --#{$prefix}body-font-size: 13px;
        --#{$prefix}body-bg: #ffffff;
        --#{$prefix}secondary-bg: #ffffff;

        --#{$prefix}theme-card-border-width: 1px;
        --#{$prefix}theme-card-border-color: #e2e7ec;
        --#{$prefix}theme-card-box-shadow: 0 4px 9px rgba(34, 7, 31, 0.05);
        --#{$prefix}box-shadow: 0 4px 9px rgba(34, 7, 31, 0.05);
        --#{$prefix}border-radius: 4px;

        --#{$prefix}font-weight-medium: 500;
        --#{$prefix}font-weight-semibold: 500;
        --#{$prefix}font-weight-bold: 600;

        --#{$prefix}font-size-xxs: 11px;
        --#{$prefix}font-size-xs: 12px;
        --#{$prefix}font-size-sm: 13px;
        --#{$prefix}font-size-base: 13px;
        --#{$prefix}font-size-md: 14px;
        --#{$prefix}font-size-lg: 16px;
        --#{$prefix}font-size-xl: 18px;
        --#{$prefix}font-size-xxl: 20px;

        // Menu items
        --#{$prefix}sidenav-item-icon-size: 1.25rem;
        --#{$prefix}sidenav-item-gap: 10px;
        --#{$prefix}sidenav-item-padding-x: 12px;
        --#{$prefix}sidenav-item-padding-y: 9px;
        --#{$prefix}sidenav-item-font-size: 13px;
        --#{$prefix}sidenav-item-font-weight: 500;

        // Sub Menu Items
        --#{$prefix}sidenav-sub-item-font-size: 13px;
        --#{$prefix}sidenav-sub-item-font-weight: 400;
        --#{$prefix}sidenav-sub-item-gap: 4px;
        --#{$prefix}sidenav-sub-item-padding-x: 10px;
        --#{$prefix}sidenav-sub-item-padding-y: 7px;

        // Light left-sidebar
        &[data-menu-color='light'] {
            --#{$prefix}sidenav-bg: #{$white};
            --#{$prefix}sidenav-border-color: #e2e7ec;
            --#{$prefix}sidenav-item-color: #{$gray-700};
            --#{$prefix}sidenav-item-hover-color: #213648;
            --#{$prefix}sidenav-item-hover-bg: #f7f7f5;
            --#{$prefix}sidenav-item-active-color: #213648;
            --#{$prefix}sidenav-item-active-bg: #f7f7f5;
        }

        // Gray left-sidebar
        &[data-menu-color='gray'] {
            --#{$prefix}sidenav-bg: #f6f7fb;
            --#{$prefix}sidenav-border-color: #e9eaf1;
            --#{$prefix}sidenav-item-color: #{$gray-700};
            --#{$prefix}sidenav-item-hover-color: #212b29;
            --#{$prefix}sidenav-item-hover-bg: #eaecf3;
            --#{$prefix}sidenav-item-active-color: #212b29;
            --#{$prefix}sidenav-item-active-bg: #eaecf3;
        }

        // Dark Left Sidebar
        &[data-menu-color='dark'] {
            --#{$prefix}sidenav-bg: #1f1e30;
            --#{$prefix}sidenav-border-color: #1f1e30;
            --#{$prefix}sidenav-item-color: #8495ab;
            --#{$prefix}sidenav-item-hover-color: #bccee4;
            --#{$prefix}sidenav-item-hover-bg: #2e2d3e;
            --#{$prefix}sidenav-item-active-color: #c0cedf;
            --#{$prefix}sidenav-item-active-bg: #2e2d3e;
        }

        // Gradient Menu
        &[data-menu-color='gradient'] {
            --#{$prefix}sidenav-bg: #{linear-gradient(90deg, #4f7fb3 0%, #3b5f86 100%)};
            --#{$prefix}sidenav-border-color: null;
            --#{$prefix}sidenav-item-color: #99b8d9;
            --#{$prefix}sidenav-item-hover-color: #cee4fb;
            --#{$prefix}sidenav-item-hover-bg: rgba(219, 236, 255, 0.05);
            --#{$prefix}sidenav-item-active-color: #cee4fb;
            --#{$prefix}sidenav-item-active-bg: rgba(219, 236, 255, 0.05);
        }

        // Image Menu
        &[data-menu-color='image'] {
            --#{$prefix}sidenav-bg: #{linear-gradient(to bottom right, rgba(16, 16, 30, 0.85), #183c52), url('../images/sidenav-bg.jpg') center/cover no-repeat};
            --#{$prefix}sidenav-border-color: null;
            --#{$prefix}sidenav-item-color: #799cb7;
            --#{$prefix}sidenav-item-hover-color: #bccee4;
            --#{$prefix}sidenav-item-hover-bg: #2f3742;
            --#{$prefix}sidenav-item-active-color: #cfdff1;
            --#{$prefix}sidenav-item-active-bg: rgba(219, 236, 255, 0.05);
        }

        // Dark Mode
        &[data-bs-theme='dark'][data-menu-color='dark'],
        &[data-bs-theme='dark'][data-menu-color='gray'],
        &[data-bs-theme='dark'][data-menu-color='light'] {
            --#{$prefix}sidenav-bg: #1e1f27;
            --#{$prefix}sidenav-border-color: #2c2d38;
            --#{$prefix}sidenav-item-color: #8495ab;
            --#{$prefix}sidenav-item-hover-color: #bccee4;
            --#{$prefix}sidenav-item-hover-bg: #2f3742;
            --#{$prefix}sidenav-item-active-color: #ced6df;
            --#{$prefix}sidenav-item-active-bg: #22232c;
        }

        // Light Topbar
        &[data-topbar-color='light'] {
            --#{$prefix}topbar-bg: #ffffff;
            --#{$prefix}topbar-item-color: #909ba5;
            --#{$prefix}topbar-item-hover-color: #3d4247;
            --#{$prefix}topbar-search-bg: transparent;
            --#{$prefix}topbar-search-border: #e7e9eb;
        }

        // Gray Topbar
        &[data-topbar-color='gray'] {
            --#{$prefix}topbar-bg: #f6f7fb;
            --#{$prefix}topbar-item-color: #{$gray-700};
            --#{$prefix}topbar-item-hover-color: #4f7fb3;
            --#{$prefix}topbar-search-bg: #eaecf3;
            --#{$prefix}topbar-search-border: #eaecf3;
        }

        // Dark Topbar
        &[data-topbar-color='dark'] {
            --#{$prefix}topbar-bg: #25313d;
            --#{$prefix}topbar-item-color: #adb5bf;
            --#{$prefix}topbar-item-hover-color: #e0eeff;
            --#{$prefix}topbar-search-bg: #313f4c;
            --#{$prefix}topbar-search-border: #313f4c;
        }

        // Gradient Topbar
        &[data-topbar-color='gradient'] {
            --#{$prefix}topbar-bg: #{linear-gradient(to bottom, #4f7fb3, #3b5f86)};
            --#{$prefix}topbar-item-color: rgba(255, 255, 255, 0.7);
            --#{$prefix}topbar-item-hover-color: #ffffff;
            --#{$prefix}topbar-search-bg: rgba(255, 255, 255, 0.1);
            --#{$prefix}topbar-search-border: rgba(255, 255, 255, 0.1);
        }

        // Topbar (Dark Mode)
        &[data-bs-theme='dark'][data-topbar-color='dark'],
        &[data-bs-theme='dark'][data-topbar-color='light'],
        &[data-bs-theme='dark'][data-topbar-color='gray'] {
            --#{$prefix}topbar-bg: #252630;
            --#{$prefix}topbar-item-color: #adb5bf;
            --#{$prefix}topbar-item-hover-color: #e0eeff;
            --#{$prefix}topbar-search-bg: #2d2e3c;
            --#{$prefix}topbar-search-border: #2d2e3c;
        }

        @each $name, $value in $theme-neo-colors {
            --#{$prefix}#{$name}: #{$value};
        }

        @each $name, $value in $theme-neo-colors {
            --#{$prefix}#{$name}-rgb: #{to-rgb($value)};
        }

        @each $name, $value in $theme-neo-colors {
            --#{$prefix}#{$name}-bg-subtle: #{rgba($value, 0.15)};
        }

        @each $name, $value in $theme-neo-colors {
            --#{$prefix}#{$name}-border-subtle: #{rgba($value, 0.3)};
        }

        @each $name, $value in $theme-neo-colors {
            --#{$prefix}#{$name}-text-emphasis: #{shade-color($value, 25%)};
        }

        // Chart Colors
        --#{$prefix}chart-primary: #4f7fb3; // Main Color
        --#{$prefix}chart-primary-rgb: #{to-rgb(#4f7fb3)};
        --#{$prefix}chart-secondary: #97a9bd; // Secondary Color
        --#{$prefix}chart-secondary-rgb: #{to-rgb(#97a9bd)};
        --#{$prefix}chart-alpha: #6fbfa5; // Success Color
        --#{$prefix}chart-alpha-rgb: #{to-rgb(#6fbfa5)};
        --#{$prefix}chart-beta: #e38c8c; // Danger Color
        --#{$prefix}chart-beta-rgb: #{to-rgb(#e38c8c)};
        --#{$prefix}chart-gamma: #e6c06f; // Warning Color
        --#{$prefix}chart-gamma-rgb: #{to-rgb(#e6c06f)};
        --#{$prefix}chart-delta: #6db6e6; // Info Color
        --#{$prefix}chart-delta-rgb: #{to-rgb(#6db6e6)};
        --#{$prefix}chart-zeta: #6070cc; // Purple or custom Color
        --#{$prefix}chart-zeta-rgb: #{to-rgb(#6070cc)};
        --#{$prefix}chart-gray: #edeff3; // Gray Color
        --#{$prefix}chart-gray-rgb: #{to-rgb(#edeff3)};
        --#{$prefix}chart-dark: #396699; // Primary Dark Color
        --#{$prefix}chart-dark-rgb: #{to-rgb(#396699)};
        --#{$prefix}chart-border-color: #ecf4fc; // Border Color
        --#{$prefix}chart-title-color: #bbcae1; // Title Color
    }

    @if $enable-dark-mode {
        @include color-mode(dark, true) {
            &[data-skin='neo'] {
                --#{$prefix}body-bg: #{#17181e};
                --#{$prefix}body-bg-rgb: #{to-rgb(#17181e)};

                --#{$prefix}secondary-bg: #{#1b1c22};
                --#{$prefix}secondary-bg-rgb: #{to-rgb(#1b1c22)};

                --#{$prefix}box-shadow: #{0px 0px 30px rgba(0, 0, 0, 0.3)};

                --#{$prefix}theme-card-border-color: #293036;

                --#{$prefix}tertiary-bg: #252630;

                --#{$prefix}chart-gray: #40424f;
                --#{$prefix}chart-gray-rgb: #{to-rgb(#40424f)};

                // Extra CSS
                .card,
                .dropdown-menu,
                .btn,
                .form-select,
                .form-control {
                    border-width: 1px !important;
                }
            }
        }
    }
}

//
//  Neo Theme
//
html[data-skin='neo'] {

    .app-topbar,
    .card,
    .sidenav-menu {
        box-shadow: none;
    }

    .app-topbar {
        &.topbar-active {
            box-shadow: var(--#{$prefix}box-shadow);
        }
    }

    .pro-username {
        font-weight: 600;
    }

    .btn {
        font-weight: normal;
    }

    .btn,
    [class*=" bg-"],
    [class^="bg-"],
    [class*=" text-bg-"],
    [class^="text-bg-"],
    .page-link,
    .nav-pills .nav-link.active,
    .nav-pills .show>.nav-link,
    .special-menu,
    .noUi-connect {
        background-image: linear-gradient(180deg, rgba(255, 255, 255, 0.3), rgba(255, 255, 255, 0));
        box-shadow: var(--#{$prefix}box-shadow-sm);
    }

    .card {
        border-style: dashed;

        .card-title {
            font-weight: 600;
        }
    }

    .topbar-link,
    .dropdown-item .btn {
        box-shadow: none !important;
        background-image: none !important;
    }
}

html[data-skin='neo'][data-bs-theme="dark"] {

    .btn,
    [class*=" bg-"],
    [class^="bg-"],
    [class*=" text-bg-"],
    [class^="text-bg-"],
    .page-link,
    .nav-pills .nav-link.active,
    .nav-pills .show>.nav-link,
    .special-menu,
    .noUi-connect {
        background-image: linear-gradient(180deg, rgba(255, 255, 255, 0.05), rgba(255, 255, 255, 0));
        box-shadow: none;
    }
}