// 
// Customizer Bar
//

.theme-customizer-bar {
    .card-radio {
        .form-check-label {
            border: none;
            background-color: transparent;
        }
    }
    .row.g-3 {
        --#{$prefix}gutter-y: 0.625rem;
        --#{$prefix}gutter-x: 0.625rem;
    }
}


//
// RTL Mode Only
//

html[dir="rtl"] {
    .offcanvas-end {
        transform: translateX(-100%);
    }
    .offcanvas-start {
        transform: translateX(100%);
    }
    .form-select {
        background-position: left .77rem center;
    }
    .pagination svg {
        transform: scaleX(-1) !important
    }

    .dropdown-item {
        i {
            display: inline-block;
        }
    }
}

